.fade-in {
  position: relative;
  opacity: 0;
  bottom: -10px;
}

.fade-in.visible {
  position: relative;
  -webkit-transition: opacity 1s linear, bottom 0.1s linear;
  -o-transition: opacity 1s linear, bottom 0.1s linear;
  transition: opacity 1s linear, bottom 0.1s linear;
  opacity: 1;
  bottom: 0px;
  -webkit-transition-delay: 0.3s;
       -o-transition-delay: 0.3s;
          transition-delay: 0.3s;
}

.mfade-in {
  opacity: 0;
}

.mfade-in.visible {
  -webkit-transition: opacity 0.2s linear;
  -o-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
  opacity: 1;
  -webkit-transition-delay: 0.5s;
       -o-transition-delay: 0.5s;
          transition-delay: 0.5s;
}

.ifade-in {
  -webkit-transition: opacity 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  opacity: 1;
}

.sg-image-style {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))),
    url(../res/bg2.jpg);
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(../res/bg2.jpg);
  background: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(../res/bg2.jpg);
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(../res/bg2.jpg);
  background-size: cover;
  background-position: center;
}

.sg-image-style.desktop {
  height: 97vh;
}

.homeStyle {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.contactSection {
  padding: 50px;
}

.mbTextStyle {
  font-size: 1.3em;
  font-weight: 100;
}

.topStyle {
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.topTitle {
  color: #9c9999;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  margin-bottom: 10px;
}

.topTitle.mobile {
  font-size: 2em;
}

.topTitle.desktop {
  font-size: 2.2em;
}

.topDescription {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
}

.topDescription.mobile {
  font-size: 3.5em;
  padding-bottom: 96px;
}

.topDescription.desktop {
  font-size: 5em;
  padding-bottom: 200px;
  padding-left: 32px;
  padding-right: 32px;
}



